import styled from "styled-components";

export const InfoSec = styled.div`
color:#fff;
padding:160px;
background:black;
`;

export const InfoRow = styled.div`
displau:flex;
margin:0 -15px -15px -15px;
flex-wrap: wrap;
align-items: center;
flex-direction : row;

`;

export const InfoColumn = styled.div`
margin-bottom:15px;
padding-right:15px;
padding-left:15px;
flex:1;
max-width:50%;
flex-basis"50%;

@media screen and (max-width:768px){
    max-width:100%;
    flex-basis:100%;
    display:flex;
    justify-content: center;
}
`;

export const TextWrapper = styled.div`
max-width:540px;
padding-top:0;
padding-bottom:60px;

@media Screen and (max-width:760px){
    padding-bottom:65px;
}
`
