import React from 'react'
import { Container } from '../../globalStyles'
import {
    InfoRow,
    InfoSec,
    InfoColumn,
    TextWrapper

} from "./infoSection.element"
const InfoSection = () => {
    return (
        <>
            <InfoSec>
                <Container>
                    <InfoRow>
                        <InfoColumn>
                            <TextWrapper>HomePage</TextWrapper>
                        </InfoColumn>
                    </InfoRow>
                </Container>
            </InfoSec>
        </>
    )
}

export default InfoSection
